.userBlock{
    font-size: small;
    display: flex;
    flex-direction: row;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: rgba(255, 217, 0, 0.199);
    width: 300px;
}

.userBlock .userInfo{
