
.paginator {
    margin-bottom: 10px;
    white-space: nowrap;
    /* max-width: 500px; */
    overflow: auto;
}

.paginator span{
    margin-left:2px;
    margin-right:2px;
    padding:2px 10px;
    display:inline-block;
    cursor: pointer;
    border-radius: 10px;
}

.paginator span:hover{
    
    background-color: greenyellow;
}


.selectedPage{
    background-color: greenyellow;
    font-weight: bold;
}