
.nav{
    grid-area: n;
    background-color: rgb(53, 145, 206);
    padding: 10px;
    color:white;
    border-radius: 3px;
    opacity: 0.75;
  }
  
  
  .item a{
    font-weight: bold;
    color:white;
    text-decoration: none;
  }

    
  .item a:hover{
   color:goldenrod
  }

  .item a.active{
    color:gold;
  }

  


  .friendsList{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  

  .friendsList div{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
  }